<template>
		<div class=" _full_router contain-white">
    <custHeader :title="title"></custHeader>
          <div class="query-date">
              <div class="normal-box">
                  <div >
                      <div>起始日期</div>
                        <dateSelect @callback="freshen" v-model="startDate" ></dateSelect>
                    </div>
                   <div>
                        <div>截止日期</div>
                        <dateSelect @callback="freshen" v-model="endDate"></dateSelect>
                   </div>
              </div>
          </div>

    			 <ul class="my-hold-container list-auto" ><li>
                <div>委托时间</div>
                <div class="text-right">委托人/金额</div>
                <div class="text-right">价格/数量</div>
                <div class="text-right">状态</div>
                </li>
                <li v-for="(item,index) in qryList">
                   <div><div>{{item.stkName}}</div><div>{{item.matchTime | timetext}}</div></div>
                   <div class="text-right"><div>{{item.custName}}</div><div >{{item.matchAmt}}</div></div>
                   <div class="text-right"><div>{{item.matchPrice}}</div><div>{{item.matchQty}}</div></div>
                   <div class="text-right"><div v-remoteoptionsspan="dicts.stkBiz">{{item.stkBiz}}</div><div v-remoteoptionsspan="dicts.orderStatus">{{item.orderStatus}}</div></div>
                </li>

            </ul>
            <div v-show="qryList.length==0" class="jqqd" >
                当前没有委托订单
            </div>

             <!--  <mt-popup
            v-model="selectTypeVisible"
            style="width:80%"
            pop-transtion="popop-fade"
            class="select-dict"
          >
            <mt-picker :slots="selectTypeSlots"  valueKey="name" @change="selectTypeChange" :visible-item-count="3"></mt-picker>
            <div class="select-dict-btns"><button class="btn" @click="dissSelectType">取消</button>
              <button class="btn" @click="sureSelectType">确认</button>
              </div>
          </mt-popup> -->
		</div>
</template>
<script>
import cookieUtil from 'js-cookie'
import {MessageBox} from 'mint-ui'
import custHeader from  'components/custHeader'
import dateSelect from  'components/dateSelect'
export default{
    data(){
       return {
       title:"认购订单查询",
       startDate:{val:""},
       endDate:{val:""},
                 qryList:[
                    ],
                 dicts:{
                      orderStatus:{
                        name:"ORDER_STATUS",
                      },
                      stkBiz:{
                        name:"STK_BIZ"
                      }
                    }
            }
    },
    created:function(){
		var date = (new Date()).Format("yyyy-M-d");
		   this.startDate={val:date.replace(/-/g,"")};
		   this.endDate={val:date.replace(/-/g,"")};
      this.upCltOrderCancelQry();
    },
     components:{
    custHeader,
    dateSelect
  },
    methods:{
      freshen:function(){
            var pars={
                sort:"orderTime",
                order:"desc",
                limit:100,
                offset:0,
                stkBiz:global.STKBIZ.SUBSCRIBE_PURCHASE,
                stkex:global.scdm,
                bgnDate:this.startDate.val,
                endDate:this.endDate.val,
            };
            axiosUtil.upCltOrderQryInfo(pars).then(data=>{
                var data=data.data;
                this.qryList=data.rows;
            })
      },

        upCltOrderCancelQry:function(){
          var date = (new Date()).Format("yyyy-M-d");
            var pars={
                sort:"orderTime",
                order:"desc",
                limit:100,
                offset:0,
                stkBiz:global.STKBIZ.SUBSCRIBE_PURCHASE,
                stkex:global.scdm,
                bgnDate:date.replace(/-/g,""),
				endDate:date.replace(/-/g,"")
            };
            axiosUtil.upCltOrderQryInfo(pars).then(data=>{
                var data=data.data;
                this.qryList=data.rows;
            })
        }
    }
}
</script>
<style type="text/css" scoped>
  .app-content{
    background: #fff;
  }
  .my-hold-container li{
    border-bottom: 1px solid #efefef;
  }
</style>



